								// 下面开始调用注册接口
								this.$http({
										act: 'user.wechatLogin',
										code: code,
										encryptedData: encryptedData,
										iv: iv
									},
									function(res) {
										uni.setStorageSync('t', response.data.t);
										uni.setStorageSync('userId', response.data.userId)
										that.$emit("modalClose");
									}
								)
							}
						})
					}
				})
			} else {
				this.$emit("modalClose");
			}
